import React from 'react';
import classNames from 'classnames/bind';

import styles from './AuthModal.module.scss';
import {
  AppleIcon,
  FacebookIcon,
  GoogleIcon,
  InstagramIcon,
  KakaoTalkIcon,
  LineIcon,
  QRCodeIcon,
  TwitterIcon,
  UserIcon,
} from '~/components/Icons';
import Button from '~/components/Button/';

const LOGIN_OPTIONS = [
  {
    icon: <QRCodeIcon width="2rem" height="2rem" />,
    title: 'S dng m QR',
  },
  {
    icon: <UserIcon width="2rem" height="2rem" />,
    title: 'S in thoi / Email / TikTok ID',
  },
  {
    icon: <FacebookIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi Facebook',
  },
  {
    icon: <GoogleIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi Google',
  },
  {
    icon: <TwitterIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi Twitter',
  },
  {
    icon: <LineIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi LINE',
  },
  {
    icon: <KakaoTalkIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi KakaoTalk',
  },
  {
    icon: <AppleIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi Apple',
  },
  {
    icon: <InstagramIcon width="2rem" height="2rem" />,
    title: 'Tip tc vi Instagram',
  },
];

const cx = classNames.bind(styles);

const Login = () => {
  return (
    <>
      <div className={cx('container')}>
        <div className={cx('login')}>
          <h1>ng nhp vo TikTok</h1>
          <ul className={cx('options')}>
            {LOGIN_OPTIONS.map((option, index) => (
              <li key={index}>
                <Button
                  className={cx('btn-option')}
                  leftIcon={option.icon}
                  rightIcon={<></>}
                  variant="secondary"
                  outline
                  rounded="none"
                  block
                >
                  {option.title}
                </Button>
              </li>
            ))}
          </ul>
        </div>
      </div>
      <div className={cx('agreement')}>
        <p>
          Bng cch tip tc, bn ng  vi{' '}
          <a target="_blank" rel="noopener noreferrer" href="https://www.tiktok.com/legal/terms-of-use">
            iu khon S dng
          </a>{' '}
          ca TikTok v xc nhn rng bn  c hiu{' '}
          <a target="_blank" rel="noopener noreferrer" href="https://www.tiktok.com/legal/privacy-policy">
            Chnh sch Quyn ring t
          </a>{' '}
          ca TikTok.
        </p>
      </div>
    </>
  );
};

export default Login;
